.program {
  padding-bottom: 31px;

  @media (min-width: $tablet-width) {
    padding-top: 0;
    padding-bottom: 45px;
  }
}

.program-group {
  display: flex;

  @media (min-width: $tablet-width) {
    display: block;
    grid-column: 1/-1;
  }
}

.program__item {
  background-color: $basic_grey;
  padding: 24px 33px 18px 22px;
  margin-bottom: 29px;

  @media (min-width: $tablet-width) {
    position: relative;
    display: grid;
    padding: 81px 33px 57px 52px;
    grid-template-columns: 70% 30%;
    margin-bottom: 30px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 0;
    padding: 80px 52px 55px 52px;
  }
}

.program__item--last-child {
  margin-bottom: 0;
  padding-top: 29px;

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
    padding-top: 70px;
  }
}

.program__wrapper {
  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 80px;
    grid-row-gap: 40px;
  }
}

.program__subtext {
  margin: 0;
  margin-bottom: 19px;
  padding-bottom: 23px;
  border-bottom: 1px solid $index_grey;
  font-family: $font_basic;
  color: $special_dark-grey;

  @media (min-width: $tablet-width) {
    padding: 0;
    margin-bottom: 52px;
    font-size: 16px;
    line-height: 24px;
    grid-column: 1/2;
    width: 344px;
    border: none;
    color: $title_black;
  }

  @media (min-width: $desktop-width) {
    width: 100%;
    grid-column: 1/-1;
    margin-bottom: 40px;
  }
}

.program__link {
  position: relative;
  display: flex;
  align-items: center;
  font-size: 16px;
  line-height: 16px;
  font-weight: 400;
  color: $title_black;
  text-decoration: none;
  font-family: $font_custom;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 30px;
  }

  &::after {
    content: "";
    display: inline-block;
    margin-left: 17px;
    width: 24px;
    height: 12px;
    background-image: url("../img/index-img/arrow.svg");
    background-position: center center;
    background-repeat: no-repeat;

    @media (min-width: $tablet-width) {
      margin-left: 25px;
    }

    @media (min-width: $desktop-width) {
      margin-left: 28px;
    }
  }

  @media (min-width: $tablet-width) {
    grid-column: 1/2;
  }

  &:active {
    opacity: 0.3;
  }

  &:hover::after {
    background-image: url("../img/index-img/arrow-big.svg");
    width: 32px;
  }
}

.program__group {
  display: flex;
  align-items: center;
  padding: 30px 0 0 0;

  @media (min-width: $tablet-width) {
    display: grid;
  }
}

/*--Title--*/

.program__title {
  position: relative;
  margin: 0;
  margin-bottom: 30px;
  padding-left: 68px;
  font-size: 24px;
  line-height: 37px;
  font-weight: 400;
  text-transform: uppercase;
  color: $title_black;
  text-align: center;

  @media (min-width: $tablet-width) {
    margin-bottom: 24px;
    grid-column: 1/2;
    font-size: 36px;
    padding-left: 0;
    text-align: left;
  }

  @media (min-width: $desktop-width) {
    padding-left: 130px;
    margin-bottom: 90px;
  }

  &::before {
    content: "";
    position: absolute;
    background-repeat: no-repeat;
    background-position: center;
  }
}

.program__item--slim .program__title::before {
  top: -5px;
  left: -6px;
  width: 50px;
  height: 50px;
  background-image: url("../img/index-img/cat__slim__svg.svg");

  @media (min-width: $tablet-width) {
    top: 4px;
    left: 63%;
    width: 200px;
    height: 211px;
    background-image: url("../img/index-img/cat-tablet-svg.svg");
  }

  @media (min-width: $desktop-width) {
    top: -5px;
    left: -6px;
    width: 100px;
    height: 100px;
    background-image: url("../img/index-img/cat-desktop-svg.svg");
  }
}

.program__item--pro .program__title::before {
  top: -7px;
  left: -17px;
  height: 52px;
  width: 70px;
  background-image: url("../img/index-img/cat__fat__svg.svg");

  @media (min-width: $tablet-width) {
    top: 16px;
    left: 58%;
    height: 200px;
    width: 268px;
    background-image: url("../img/index-img/cat-tablet-pro-svg.svg");
  }

  @media (min-width: $desktop-width) {
    top: -7px;
    left: -17px;
    height: 100px;
    width: 134px;
    background-image: url("../img/index-img/cat-desktop-pro-svg.svg");
  }
}

.program__buttons {
  grid-column: 1/2;
  display: flex;
  align-items: center;
}

.program__button {
  display: inline-block;
  width: 25px;
  height: 10px;
  margin-left: 4px;
  background-image: url("../img/index-img/arrow.svg");
  background-repeat: no-repeat;
  background-position: center center;

  @media (min-width: $tablet-width) {
    width: 25px;
    margin-left: 25px;
  }

  @media (min-width: $desktop-width) {
    margin-left: 27px;
  }

  &:hover {
    background-image: url("../img/index-img/arrow-big.svg");
    width: 32px;
  }

  &:active {
    opacity: 0.3;
  }
}
